import {
  collection,
  addDoc,
  getDocs,
  getDoc,
  setDoc,
  updateDoc,
  deleteDoc,
  doc,
  query,
  where,
  serverTimestamp,
} from 'firebase/firestore';
import { db } from '../firebaseConfig';

const PROJECTS_COLLECTION = 'projects';
const USERS_COLLECTION = 'users';

// 
export const createProject = async (userId, projectData) => {
  try {
    console.log('DEBUG: createProject called with userId:', userId);
    console.log('DEBUG: projectData:', projectData);
    const docRef = await addDoc(
      collection(db, PROJECTS_COLLECTION),
      {
        userId,
        ...projectData,
        createdAt: serverTimestamp(),
        updatedAt: serverTimestamp(),
      }
    );
    console.log(' :', docRef.id);
    console.log('DEBUG:  ID:', docRef.id);
    return docRef.id;
  } catch (error) {
    console.error(' :', error);
    console.error('ERROR CODE:', error.code);
    console.error('ERROR MESSAGE:', error.message);
    throw error;
  }
};

// 
export const getProjects = async (userId) => {
  try {
    console.log('DEBUG: getProjects called with userId:', userId);
    const q = query(
      collection(db, PROJECTS_COLLECTION),
      where('userId', '==', userId)
    );
    const snapshot = await getDocs(q);
    const projects = snapshot.docs.map(doc => ({
      id: doc.id,
      ...doc.data(),
    }));
    
    // JavaScript
    projects.sort((a, b) => {
      const timeA = a.createdAt?.toMillis?.() || 0;
      const timeB = b.createdAt?.toMillis?.() || 0;
      return timeB - timeA;
    });
    
    console.log(' :', projects.length, '');
    console.log('DEBUG: :', projects);
    return projects;
  } catch (error) {
    console.error(' :', error);
    console.error('ERROR CODE:', error.code);
    console.error('ERROR MESSAGE:', error.message);
    throw error;
  }
};

export const updateProject = async (projectId, updates) => {
  try {
    const projectRef = doc(db, PROJECTS_COLLECTION, projectId);
    await updateDoc(projectRef, {
      ...updates,
      updatedAt: serverTimestamp(),
    });
    console.log(' :', projectId);
  } catch (error) {
    console.error(' :', error);
    throw error;
  }
};

export const deleteProject = async (projectId) => {
  try {
    const projectRef = doc(db, PROJECTS_COLLECTION, projectId);
    await deleteDoc(projectRef);
    console.log(' :', projectId);
  } catch (error) {
    console.error(' :', error);
    throw error;
  }
};

